.summary {
    text-align: center;
    max-width: 45rem;
    width: 90%;
    margin: auto;
    margin-top: -10rem;
    position: relative;
    background-color: #383838c5;
    color: white;
    border-radius: 14px;
    padding: 1.5rem;
    box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
    z-index: 9;
  }
  .black{
    background-color: rgba(0, 0, 0, 0.397);
  }
 
  .summary h2 {
    font-size: 2rem;
    margin-top: 0;
  }